import angular from 'angular';
import 'angular-material';
import mainwrap from 'common/directives/mainwrap/mainwrap';
import './forms.tpl';
import './forms.css!';

const formModule = angular.module('forms', [
  mainwrap.name,
  'app/forms/forms.tpl.html'
]);

formModule.config(function($stateProvider){
  $stateProvider.state('forms', {
    url: '/forms',
    templateUrl: 'app/forms/forms.tpl.html',
    controller: 'FormsCtrl',
    controllerAs: 'formsCtrl'
  });
});

formModule.controller('FormsCtrl', $scope => {
  $scope = $scope;
  console.log('forms!');
});

export default formModule;
